import React, { Component } from 'react';

class Contact extends Component {
    render() {
        return (
            <div id="contact">
                    <div className="card card-body bg-info section-padding pb-5">
                        <h2>Contact</h2>
                    </div>
                    <div className="card card-body py-5">
                    <img className="img-fluid rounded-cirlce header-image mx-auto d-block" src="imgs/me.jpg" alt="Me"/>
                        <h3>Get in Touch</h3>
                        <p className="lead">The best way to get in contact with me is through my Email: <a href="#">manzanero.luis1995@gmail.com</a>. You can also use the form below to reach out to me directly to my email. I hope that you're convinced, Thank you!</p>
                        <form>
                           <div className="form-group">
                               <div className="input-group input-group-lg">
                                   <span className="input-group-addon bg-info text-white">
                                       <i className="fa fa-user"></i>
                                   </span>
                                   <input type="text" className="form-control bg-dark text-white" placeholder="Name"/>
                               </div>
                           </div> 
                           <div className="form-group">
                                <div className="input-group input-group-lg">
                                    <span className="input-group-addon bg-info text-white">
                                        <i className="fa fa-envelope"></i>
                                    </span>
                                    <input type="email" className="form-control bg-dark text-white" placeholder="Email"/>
                                </div>
                            </div> 
                            <div className="form-group">
                               <div className="input-group input-group-lg">
                                   <span className="input-group-addon bg-info text-white">
                                       <i className="fa fa-pencil"></i>
                                   </span>
                                   <textarea className="form-control bg-dark text-white" placeholder="Message"></textarea>
                               </div>
                           </div> 
                           <input type="submit" value="submit" className="btn btn-info btn-block btn-lg"/>
                        </form>
                    </div>
                </div>
        );
    }
}

export default Contact;


